import {Api, profileAPI, usersAPI} from "../api/api";
import {setInitializedSucsess} from "./appReducer";

const ADD_POST = 'profile/ADD-POST';
const SET_USER_PROFILE = 'profile/SET_USER_PROFILE';
const SET_STATUS = 'profile/SET_STATUS';
const DELETE_POST = 'profile/DELETE_POST';
const SAVE_PHOTO_SUCSESS = 'profile/SAVE_PHOTO_SUCSESS';

let initialState = {
    posts: [
        {id: 1, message: 'Hi, how are you?', likesCount: 12},
        {id: 2, message: 'It\'s my first post', likesCount: 11},
        {id: 3, message: 'Blabla', likesCount: 11},
        {id: 4, message: 'Dada', likesCount: 11}
    ],
    profile: null,
    status: ""
};

const profileReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_POST: {
            let newPost = {
                id: 5,
                message: action.newPostText,
                likesCount: 0
            };
            return {
                ...state,
                posts: [...state.posts, newPost],
                newPostText: ''
            };
        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }
        case SET_USER_PROFILE: {
            return {...state, profile: action.profile}
        }
        case SAVE_PHOTO_SUCSESS: {
            return {...state, profile: {...state.profile, photos: action.photos}}
        }

        case DELETE_POST:
            return {...state, posts: state.posts.filter(p => p.id != action.postId)}
        default:
            return state;
    }
}


export const addPostSucsess = (newPostText) => ({type: ADD_POST, newPostText})
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile})
export const setStatus = (status) => ({type: SET_STATUS, status})
export const deletePost = (postId) => ({type: DELETE_POST, postId})
export const savePhotoSucsess = (photos) => ({type: SAVE_PHOTO_SUCSESS, photos})

export const getUserData = (userId) => async (dispatch) => {
    dispatch(setInitializedSucsess(true))
    await dispatch(getUserProfile(userId))
    await dispatch(getStatus(userId))
    dispatch(setInitializedSucsess(false))
}

export const getUserProfile = (userId) => async (dispatch) => {
    const response = await Api.profile.getUserProfile_api(userId);
    dispatch(setUserProfile(response));
}
export const savePhoto = (filePhoto) => async (dispatch) => {
    debugger
    const response = await Api.profile.setProfilePhoto_api(filePhoto);
    debugger
    dispatch(savePhotoSucsess(response.data.photos));
}

export const getStatus = (userId) => async (dispatch) => {
    let response = await Api.profile.getUserProfileStatus_api(userId);
    dispatch(setStatus(response));
}

export const updateStatus = (status) => async (dispatch) => {
    let response = await Api.profile.setUserProfileStatus_api(status);

    if (response.resultCode === 0) {
        dispatch(setStatus(status));
    }
}

export default profileReducer;